import {
  CDetailUser,
  CError,
  CLoadingSpinner,
  CPageTitle,
  CTitle,
} from "@components";
import { FGetAccess, FGetData } from "@function";
import { Button, Card, Col, Row, Tabs } from "antd";
import { useRouter } from "next/router";
import React, { useState, useEffect } from "react";

const DetailUser = () => {
  const router = useRouter();
  const { id } = router.query;

  const [isLoading, setIsLoading] = useState(false);
  const [accessList, setAccessList] = useState({ view: true });
  const [forms, setForms] = useState({
    id: "",
    address: "",
    code: "",
    contact: "",
    name: "",
    opening_date: "",
    phone: "",
    postal: "",
  });

  useEffect(() => {
    FGetAccess(router, setAccessList, setIsLoading);
    FGetData("api/master/user/" + id, setForms);
  }, []);

  if (isLoading) return <CLoadingSpinner />;
  if (!accessList.view) return <CError status={403} />;

  return (
    <>
      <CTitle />
      <Card
        cover={
          <Col
            span={24}
            style={{ backgroundColor: "#1890ff", paddingTop: "5px" }}
          />
        }
      >
        <Row>
          <Col span={22}>
            <CPageTitle title={forms.name} />
          </Col>
          <Col span={2}>
            <Button
              type="primary"
              onClick={() => router.back()}
              style={{ float: "right" }}
            >
              Back
            </Button>
          </Col>
        </Row>
        <Row gutter={4} style={{ flex: 1 }}>
          <Col span={24}>
            <Tabs
              defaultActiveKey="1"
              items={[
                {
                  label: "Data",
                  key: "Data",
                  children: <CDetailUser data={forms} />,
                },
              ]}
            />
          </Col>
        </Row>
      </Card>
    </>
  );
};

export default DetailUser;
